.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.pay_img-remove-btn:active {
  background-color: red !important;
}
.reviewInput::placeholder {
  color: "red" !important;
}
.sliderT9 {
  background: #ff7676 !important;
  height: 3px;
}
.thumb.thumb-0 {
  top: -29px;
}
.thumb.thumb-0.active {
  border: none !important;
}
.range-slider-container {
  display: flex;
  align-items: center;
}

.slider {
  width: 300px;
  margin-right: 20px;
}

.icon-container {
  display: flex;
  align-items: center;
}

.icon {
  width: 40px;
  height: 40px;
}
.slider {
  -webkit-appearance: none;
  width: 50%;
  height: 4%;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  /* opacity: 0.7; */
  /* -webkit-transition: 0.2s;
  transition: opacity 0.2s; */
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: url("./assets/icons/fire.svg");
  border-radius: 50%;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: url("./assets/icons/fire.svg");
  border-radius: 50%;
  cursor: pointer;
}

.scroll-loop-container {
  overflow: hidden;
  width: 100%;
}

.scroll-loop {
  display: flex;
  animation: scrollAnimation 20s linear infinite;
}

/* Добавьте стили для элементов внутри скролла, чтобы они отображались горизонтально */
.scroll-loop > * {
  flex-shrink: 0;
}

@keyframes scrollAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(
      -200%
    ); /* Измените -200% на -(ширина всех элементов) */
  }
}

.scroll-item {
  /* Добавьте здесь стили, которые вы хотите применить к элементам */
  /* Например: */
  width: 200px;
  height: 100px;
  background-color: #f0f0f0;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.loader {
  width: 48px;
  height: 48px;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #ae621a transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.slide-img {
  width: 100%;
  height: 400px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  transition: opacity 0.5s ease-in-out; /* Add a transition for the opacity property */
}

/* Add a class for the fading out effect */
.slide-img.fade-out {
  opacity: 0;
}
.slick-fade.slick-slide img {
  transition: 0s !important;
}
